import React, { useState, useEffect } from 'react';
import { Row, Col, Card, CardBody, Media, Nav, NavItem, NavLink, TabContent, TabPane, } from 'reactstrap';
import ImageUpload from './ImageHandler';
import CatalogProducts from '../catalog/CatalogProducts';
import { DownOutlined } from '@ant-design/icons';
import ReactQuill from 'react-quill';
import MediaLib from './MediaLib';

import "react-quill/dist/quill.snow.css";

import classNames from 'classnames';
import {
    Button,
    Switch,
    Form,
    Input,
    Select,
    Tree,
    Space
} from 'antd';

const { Option } = Select;

const formItemLayout = {
    labelCol: {
        xs: {
            span: 24,
        },
        sm: {
            span: 8,
        },
    },
    wrapperCol: {
        xs: {
            span: 24,
        },
        sm: {
            span: 16,
        },
    },
};
const tailFormItemLayout = {
    wrapperCol: {
        xs: {
            span: 24,
            offset: 0,
        },
        sm: {
            span: 16,
            offset: 8,
        },
    },
};

const modules = {
    toolbar: [
        [{ header: [1, 2, false] }],
        ["bold", "italic", "underline", "strike", "blockquote"],
        [
            { list: "ordered" },
            { list: "bullet" },
            { indent: "-1" },
            { indent: "+1" },
        ],
        ["link", "code"],
        ["clean"],
    ],
};

const formats = [
    "header",
    "bold",
    "italic",
    "underline",
    "strike",
    "blockquote",
    "list",
    "bullet",
    "indent",
    "link",
    "code",
];



//{ onFinish, handleFileListChange, fileList,catalogInitVal, catalog_active, catalog_enable, catalog_name, catalog_intro, catalog_description }
const CatalogEffect = (props) => {
    const [form] = Form.useForm();

    const { treeData, initialValues, onFinish, handleFileListChange, activeTab, fileList, toggleTab, handleTreeSelect,
        catalog_active, catalog_enable, catalog_name, catalog_intro, catalog_description, onEditorContentChange } = props;

    const [catalogTree, setCatalogTree] = useState([treeData]);

    useEffect(() => {
        setCatalogTree(treeData);


    }, [treeData]);

    const [catalogNodeSelect, setCatalogNode] = useState([]);
    const [expandedKeys, setExpandedKeys] = useState(['0-0-0-0']);

    const handleInnerTreeSelect = (selectedKeys) => {
        form.setFieldsValue({
            catalog_active: false,
            catalog_enable: true,
            catalog_name: selectedKeys[0],
            catalog_intro: 'catalog_intro',
            catalog_description: 'catalog_description',
        });
        setCatalogNode(selectedKeys);
        toggleTab('detail');
    }

    const handleAddChild = () => {
        form.resetFields();
        //setExpandedKeys(catalogNodeSelect[0]);
        setExpandedKeys(catalogNodeSelect);
        console.log('current Tree node:', catalogNodeSelect[0]);
        // const newJson = buildNewNestedJson(catalogTree);
        // console.log('handleAddChild-newJson:', newJson);
        // setCatalogTree(newJson);
    }

    const newNode = {
        title: 'newNode',
        value: 'newNode',
        key: '0',
    }
    const [file, setFile] = useState(null);

    const buildNewNestedJson = (originalJson) => {
        console.log('handleAddChild2:', catalogNodeSelect[0]);
        let newJson = [];

        for (var key in originalJson) {
            if (originalJson.hasOwnProperty(key)) {
                var value = originalJson[key];

                if (typeof value === 'object' && value !== null) {
                    if (value.key === catalogNodeSelect[0]) {
                        if (typeof value.children !== 'undefined') {
                            console.log('current children', value.children);
                            value.children.push(newNode);
                        }
                        else {
                            value.children = newNode;
                        }
                    }
                    newJson[key] = buildNewNestedJson(value); // Recursively build nested JSON
                }
                else {
                    newJson[key] = value;
                }
            }

        }
        return newJson;
    }

    const onDragEnter = (info) => {
        console.log(info);
        // expandedKeys, set it when controlled is needed
        // setExpandedKeys(info.expandedKeys)
    };

    const onDrop = (info) => {
        console.log(info);
        const dropKey = info.node.key;
        const dragKey = info.dragNode.key;
        const dropPos = info.node.pos.split('-');
        const dropPosition = info.dropPosition - Number(dropPos[dropPos.length - 1]);
        const loop = (data, key, callback) => {
            for (let i = 0; i < data.length; i++) {
                if (data[i].key === key) {
                    return callback(data[i], i, data);
                }
                if (data[i].children) {
                    loop(data[i].children, key, callback);
                }
            }
        };
        const data = [...catalogTree];

        // Find dragObject
        let dragObj;
        loop(data, dragKey, (item, index, arr) => {
            arr.splice(index, 1);
            dragObj = item;
        });
        if (!info.dropToGap) {
            // Drop on the content
            loop(data, dropKey, (item) => {
                item.children = item.children || [];
                // where to insert. New item was inserted to the start of the array in this example, but can be anywhere
                item.children.unshift(dragObj);
            });
        } else if (
            (info.node.props.children || []).length > 0 &&
            // Has children
            info.node.props.expanded &&
            // Is expanded
            dropPosition === 1 // On the bottom gap
        ) {
            loop(data, dropKey, (item) => {
                item.children = item.children || [];
                // where to insert. New item was inserted to the start of the array in this example, but can be anywhere
                item.children.unshift(dragObj);
                // in previous version, we use item.children.push(dragObj) to insert the
                // item to the tail of the children
            });
        } else {
            let ar = [];
            let i;
            loop(data, dropKey, (_item, index, arr) => {
                ar = arr;
                i = index;
            });
            if (dropPosition === -1) {
                ar.splice(i, 0, dragObj);
            } else {
                ar.splice(i + 1, 0, dragObj);
            }
        }
        setCatalogTree(data);
    };



    return (
        <Form
            {...formItemLayout}
            form={form}
            // initialValues={{
            //     catalog_treecatalog: expandedKeys,
            //     catalog_active: false,
            //     catalog_enable: true,
            //     catalog_name: '',
            //     catalog_intro: '',
            //     catalog_description: '',
            // }}
            name="catalog_form"
            onFinish={onFinish}
            labelCol={{
                span: 4,
            }}
            wrapperCol={{
                span: 18,
            }}
            scrollToFirstError
        >
            <Row>
                <Col xl={3}>
                    <Row>
                        <Col md={12}>
                            <Card >
                                <CardBody style={{ backgroundColor: "#f7f7f7" }}>
                                    <Row>
                                        <Col>
                                            <React.Fragment>

                                                <Media className="mt-2 mb-2 p-3" style={{ backgroundColor: "#fff" }}>
                                                    <Media body >
                                                        <Form.Item name="catalog_treecatalog" className="mt-0 mb-0 p-2">
                                                            <h6 className="mt-0 mb-2 header-title"></h6>
                                                            <p className="mt-1 mb-3" style={{ fontSize: "12px" }}>
                                                                </p>

                                                            <Tree
                                                                showLine
                                                                switcherIcon={<DownOutlined />}
                                                                defaultExpandAll={true}
                                                                //defaultExpandedKeys={expandedKeys}
                                                                defaultExpandParent={expandedKeys}
                                                                draggable
                                                                //blockNode
                                                                onDragEnter={onDragEnter}
                                                                onDrop={onDrop}
                                                                defaultSelectedKeys={expandedKeys}
                                                                onSelect={handleInnerTreeSelect}
                                                                treeData={catalogTree}
                                                            />
                                                        </Form.Item>
                                                        {/* <Form.Item {...tailFormItemLayout}> */}
                                                        <Space size="middle">
                                                            <div className="mt-4" style={{ float: "right" }}>
                                                                <span className="dtr-data">
                                                                    <button onClick={handleAddChild} className="btn btn-primary mr-2" data-toggle="modal" data-target="#bs-role-modal-xl"></button>
                                                                    {/* <Button htmlType="button" onClick={handleAddRoot}>
                                                                            Reset
                                                                        </Button> */}
                                                                    {/* <Button type="link" htmlType="button" onClick={handleAddRoot(catalogNodeSelect)}>
                                                                            Fill form
                                                                        </Button> */}
                                                                </span>
                                                            </div>
                                                        </Space>
                                                        {/* </Form.Item> */}

                                                    </Media>
                                                </Media>


                                            </React.Fragment>
                                        </Col>

                                    </Row>
                                </CardBody>
                            </Card>
                        </Col>
                    </Row>

                </Col>
                <Col xl={9}>
                    <Row>
                        <Col md={12}>
                            <Card>
                                <CardBody>
                                    <Nav tabs>
                                        <NavItem>
                                            <NavLink
                                                href="#"
                                                className={classNames({ active: activeTab === 'detail' })}
                                                onClick={() => { toggleTab('detail'); }}></NavLink>
                                        </NavItem>
                                        <NavItem>
                                            <NavLink
                                                href="#"
                                                className={classNames({ active: activeTab === 'advance' })}
                                                onClick={() => { toggleTab('advance'); }}></NavLink>
                                        </NavItem>
                                        <NavItem>
                                            <NavLink
                                                href="#"
                                                className={classNames({ active: activeTab === 'preview' })}
                                                onClick={() => { toggleTab('preview'); }}></NavLink>
                                        </NavItem>
                                    </Nav>
                                    <TabContent activeTab={activeTab}>
                                        <TabPane tabId="detail">
                                            <Row>
                                                <Col sm="12">
                                                    <Card >
                                                        <CardBody style={{ backgroundColor: "#f7f7f7" }}>
                                                            <Row>
                                                                <Col>
                                                                    <Media className="mt-2 mb-2 p-3" style={{ backgroundColor: "#fff", fontSize: "15px" }}>
                                                                        <Media body >
                                                                            <h6 className="mt-0 mb-2 header-title"></h6>
                                                                            <hr></hr>
                                                                            <Form.Item name="catalog_active" label="" valuePropName="checked" className="mt-0 mb-0 p-2">
                                                                                <Switch value={catalog_active} />
                                                                            </Form.Item>
                                                                            <Form.Item name="catalog_enable" label="" valuePropName="checked" className="mt-0 mb-0 p-2">
                                                                                <Switch value={catalog_enable} />
                                                                            </Form.Item>
                                                                            <Form.Item
                                                                                name="catalog_name"
                                                                                label=""
                                                                                value={catalog_name}
                                                                                tooltip="?"
                                                                                rules={[
                                                                                    {
                                                                                        required: true,
                                                                                        message: '',
                                                                                        whitespace: true,
                                                                                    },
                                                                                ]}
                                                                            ><Input />
                                                                            </Form.Item>
                                                                            <Form.Item
                                                                                name="catalog_intro"
                                                                                label=""
                                                                                value={catalog_intro}
                                                                                rules={[
                                                                                    {
                                                                                        required: true,
                                                                                        message: '',
                                                                                    },
                                                                                ]}
                                                                            >
                                                                                <Input.TextArea showCount maxLength={100} />
                                                                            </Form.Item>
                                                                            <Form.Item
                                                                                style={{ minHeight: '120px' }}
                                                                                name="catalog_description"
                                                                                label=""
                                                                                rules={[
                                                                                    {
                                                                                        required: true,
                                                                                        message: '',
                                                                                    },
                                                                                ]}
                                                                            >
                                                                                <ReactQuill
                                                                                    theme="snow"
                                                                                    value={catalog_description || ""}
                                                                                    modules={modules}
                                                                                    formats={formats}
                                                                                    onChange={onEditorContentChange}
                                                                                    placeholder={catalog_description}
                                                                                />
                                                                            </Form.Item>
                                                                            <Form.Item
                                                                                name="catalog_image"
                                                                                label=""
                                                                            >
                                                                                <p className="mt-1 mb-3" style={{ fontSize: "12px" }}>
                                                                                     *.png, *.jpg and *.jpeg </p>
                                                                                <ImageUpload onFileListChange={handleFileListChange} fileList={fileList}></ImageUpload>
                                                                            </Form.Item>
                                                                            <Form.Item
                                                                                name="catalog_media_lib"
                                                                                label=""
                                                                            >
                                                                                <MediaLib></MediaLib>
                                                                            </Form.Item>
                                                                            <Form.Item
                                                                                name="catalog_template"
                                                                                label=""
                                                                                rules={[
                                                                                    {
                                                                                        required: true,
                                                                                        message: '',
                                                                                    },
                                                                                ]}
                                                                            >
                                                                                {/* <h6 className="mt-0 mb-2 header-title"></h6>
                                                            <p className="mt-1 mb-3" style={{ fontSize: "12px" }}>
                                                                </p> */}

                                                                                <Select name="catalog_template"
                                                                                    className="mb-3 react-select"
                                                                                    options={[
                                                                                        { value: 'wigs', label: 'wigs' },
                                                                                        { value: 'sports', label: 'sports' },
                                                                                        { value: 'furniture', label: 'furniture' },
                                                                                    ]}></Select>
                                                                            </Form.Item>
                                                                            <Form.Item {...tailFormItemLayout}>
                                                                                <Button type="primary" htmlType="submit">
                                                                                    
                                                                                </Button>
                                                                            </Form.Item>
                                                                        </Media>
                                                                    </Media>
                                                                </Col>

                                                            </Row>
                                                        </CardBody>

                                                        <CardBody style={{ backgroundColor: "#f7f7f7" }}>
                                                            <Row>
                                                                <Col>
                                                                    <React.Fragment>


                                                                    </React.Fragment>
                                                                </Col>

                                                            </Row>
                                                        </CardBody>

                                                    </Card>

                                                </Col>
                                            </Row>
                                        </TabPane>
                                        <TabPane tabId="advance">
                                            <Row>
                                                <Col sm="12">
                                                    <Card >
                                                        <CardBody style={{ backgroundColor: "#f7f7f7" }}>
                                                            <Row>
                                                                <Col>
                                                                    <Media className="mt-2 mb-2 p-3" style={{ backgroundColor: "#fff" }}>
                                                                        <Media body >
                                                                            <CatalogProducts></CatalogProducts>
                                                                        </Media>

                                                                    </Media>
                                                                </Col>

                                                            </Row>
                                                        </CardBody>

                                                        <CardBody style={{ backgroundColor: "#f7f7f7" }}>
                                                            <Row>
                                                                <Col>
                                                                    <React.Fragment>

                                                                    </React.Fragment>
                                                                </Col>

                                                            </Row>
                                                        </CardBody>

                                                    </Card>
                                                </Col>
                                            </Row>
                                        </TabPane>
                                        <TabPane tabId="preview">
                                            <Row>
                                                <Col sm="12">
                                                    preview
                                                    {/* <Sessions Sections={currentCourse} SectionBottom={this.state.sectionBottom}></Sessions> */}
                                                </Col>
                                            </Row>
                                        </TabPane>
                                    </TabContent>

                                </CardBody>
                            </Card>
                        </Col>
                    </Row>

                </Col>
            </Row>

        </Form>

    );
};

export default CatalogEffect;